.block-description-series__image,
.block-description-series__content {
  display: none;
}

.block-description-series__content {
  padding: 20px 20px 20px 30px;
  font-size: 20px;
  line-height: 20;
  font-weight: 500;
}

.block-description-series__content p {
  line-height: 28px;
  margin: 10px;
}

.block-description-series__content *:first-child {
  margin-top: 0;
}

.block-description-series__content ul {
  margin: 10px 0;
}

.block-description-series__content li {
  line-height: 50px;
}

.block-description-series__content.opened {
  display: block;
  font-size: 15px;
  line-height: 28px;
  padding-left: 0px;
}

.block-description-series__content.opened p {
  margin: 0;
}

.block-description-series__content.opened ul {
  margin: 10px 0;
  padding: 0;
  padding-left: 30px;
}

.block-description-series__content.opened li {
  line-height: 28px;
}

.block-description-series__content-mobile-only {
  display: none;
}

.block-description-series__content-mobile-only.closed p {
  display: none;
}

.block-description-series__link-toggle.toggled {
  font-size: 0;
}

.block-description-series__link-toggle.toggled span {
  font-size: 15px;
  display: block;
}

@media only screen and (min-width: 768px) {
  .block-description-series {
    display: grid;
    grid-template-columns: auto auto;
    margin-bottom: 40px !important;
  }

  .block-description-series__image,
  .block-description-series__content {
    display: block;
  }

  .block-description-series__content {
    font-size: 15px;
    line-height: 20px;
  }
}

@media only screen and (max-width: 767px) {
  .block-description-series__content {
    font-size: 15px;
  }

  .block-description-series__content-mobile-only {
    display: block;
    font-weight: 500;
    font-size: 15px;
    line-height: 28px;
    margin-bottom: 40px;
  }

  .block-description-series__content-mobile-only p {
    margin: 0;
  }
}